.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

* {
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  color: #FFFFFF;
  letter-spacing: .2px;
  text-rendering: optimizeLegibility;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Open Sans", serif;
  font-weight: 400;
  letter-spacing: .5px;
  color: #48ACF0;
}

.btn {
  background-color: #2B2B2B;
  color: #B6B6B6;
  font-size: 16px;
  font-weight: 300;
  text-transform: uppercase;
  border: none;
  border-radius: 2px;
  padding: 8px 15px;
  cursor: pointer;
  transform: scale(1);
  transition: all .1s;
  box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24), 0 1.5px 6px rgba(0, 0, 0, 0.12);
}

.btn:hover, .btn:focus {
  box-shadow: 0 3px 12px rgba(0, 0, 0, 0.23), 0 3px 12px rgba(0, 0, 0, 0.16);
  outline: none;
  transform: scale(1.02);
  transition: all .1s;
}

.btn:active {
  box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24), 0 1.5px 6px rgba(0, 0, 0, 0.12);
  outline: none;
  transform: scale(1);
  transition: all .1s;
}

.btn + .btn {
  margin-left: 15px;
}

.btn-primary {
  background-color: #48ACF0;
  color: #2B2B2B;
}

.btn-footer {
  text-align: center;
  border-top: 1px solid #2B2B2B;
  background-color: #48ACF0;
  margin: 40px -40px -40px;
  padding: 20px 40px;
}

.card, .jcf-form-wrap {
  box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24), 0 1.5px 6px rgba(0, 0, 0, 0.12);
}

.card hr, .jcf-form-wrap hr {
  border: none;
  border-bottom: 1px solid #2B2B2B;
  margin: 40px -40px;
}

.card .heading, .jcf-form-wrap .heading {
  box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24), 0 1.5px 6px rgba(0, 0, 0, 0.12);
  background-color: #48ACF0;
  color: rgba(255, 255, 255, 0.5);
  border-top: 10px solid #48ACF0;
  padding: 20px 40px;
  margin: -40px -40px 40px;
}

.card .heading h1, .jcf-form-wrap .heading h1, .card .heading h2, .jcf-form-wrap .heading h2, .card .heading h3, .jcf-form-wrap .heading h3, .card .heading h4, .jcf-form-wrap .heading h4, .card .heading h5, .jcf-form-wrap .heading h5, .card .heading h6, .jcf-form-wrap .heading h6 {
  color: #2B2B2B;
  margin: 0px;
  padding: 0px;
}

.card .heading p, .jcf-form-wrap .heading p {
  font-size: .8em;
  margin: 10px 0px 0px;
  padding: 0px;
}

.float-label-active, .jcf-form-wrap input:focus + label .float-label, .jcf-form-wrap input.active + label .float-label {
  position: relative;
  top: -22.5px;
  font-size: .7em;
  color: #B6B6B6;
}

.jcf-form-wrap {
  box-sizing: border-box;
  width: 100%;
  max-width: 100%;
  background-color: transparent;
  color: #FFFFFF;
  font-family: "Roboto", sans-serif;
  font-size: 15px;
  margin-left: auto;
  margin-right: auto;
  padding: 40px;
}

.jcf-form-wrap * {
  box-sizing: border-box;
}

.jcf-form-wrap .jcf-input-group {
  position: relative;
  margin-bottom: 30px;
}

.jcf-form-wrap input {
  width: 100%;
  background-color: transparent;
  color: #FFFFFF;
  font-size: 22.5px;
  font-weight: 300;
  border: none;
  border-bottom: 1px solid #2B2B2B;
  margin: 0px;
  padding: 0px 10px 10px;
}

.jcf-form-wrap input:focus {
  box-shadow: none;
  outline: none;
}

.jcf-form-wrap input:focus + label:after {
  width: 100%;
  transition: all 0.5s;
}

.jcf-form-wrap input:focus + label .float-label {
  color: #48ACF0;
}

.jcf-form-wrap label {
  position: absolute;
  font-weight: normal;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  margin: 0px;
  padding: 0px 10px;
}

.jcf-form-wrap label:after {
  content: "";
  position: absolute;
  bottom: -1px;
  left: 0%;
  height: 3px;
  width: 0%;
  background-color: #48ACF0;
  transition: all 0.2s;
}

.jcf-form-wrap label .float-label {
  position: relative;
  top: 0px;
  transition: all 0.2s;
}

.jcf-form-wrap .input-question {
  font-size: .9em;
  font-weight: 600;
  color: #B6B6B6;
  margin-bottom: 10px;
}

