  padding-left: 20px;
  cursor: pointer;
  margin-bottom: 3px;
}

.jcf-form-wrap .input-checkbox input,
.jcf-form-wrap .input-radio input {
  width: auto;
  opacity: 0;
  margin-left: -20px;
  margin-right: 10px;
