.jcf-form-wrap {
  box-sizing: border-box;
  width: 100%;
  max-width: 100%;
  background-color: transparent;
  color: #FFFFFF;
  font-family: "Roboto", sans-serif;
  font-size: 15px;
  margin-left: auto;
  margin-right: auto;
  padding: 40px;
}

.jcf-form-wrap * {
  box-sizing: border-box;
}

.jcf-form-wrap .input-group {
  position: relative;
  margin-bottom: 30px;
}

.jcf-form-wrap input {
  width: 100%;
  background-color: transparent;
  color: #FFFFFF;
  font-size: 22.5px;
  font-weight: 300;
  border: none;
  border-bottom: 1px solid #2B2B2B;
  margin: 0px;
  padding: 0px 10px 10px;
}

.jcf-form-wrap input:focus {
  box-shadow: none;
  outline: none;
}

.jcf-form-wrap input:focus + label:after {
  width: 100%;
  transition: all 0.5s;
}

.jcf-form-wrap input:focus + label .float-label {
  color: #48ACF0;
}

.jcf-form-wrap label {
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  padding: 0px 10px;
}

.jcf-form-wrap label:after {
  content: "";
  position: absolute;
  bottom: -1px;
  left: 0%;
  height: 3px;
  width: 0%;
  background-color: #48ACF0;
  transition: all 0.2s;
}

.jcf-form-wrap label .float-label {
  position: relative;
  top: 0px;
  transition: all 0.2s;
}

.jcf-form-wrap .input-question {
  font-size: .9em;
  font-weight: 600;
  color: #B6B6B6;
  margin-bottom: 10px;
}

.jcf-form-wrap .input-checkbox label,
.jcf-form-wrap .input-radio label {
  position: relative;
  display: block;
  padding-left: 20px;
  cursor: pointer;
  margin-bottom: 3px;
}

.jcf-form-wrap .input-checkbox input,
.jcf-form-wrap .input-radio input {
  width: auto;
  opacity: 0;
  margin-left: -20px;
  margin-right: 10px;
}

.jcf-form-wrap .input-checkbox input:checked + .icon:before,
.jcf-form-wrap .input-radio input:checked + .icon:before {
  content: '';
  color: #48ACF0;
}

.jcf-form-wrap .input-checkbox .icon,
.jcf-form-wrap .input-radio .icon {
  display: inline;
}

.jcf-form-wrap .input-checkbox .icon:before,
.jcf-form-wrap .input-radio .icon:before {
  content: '';
  font-family: 'icomoon';
  position: absolute;
  left: 4px;
  top: -2px;
  font-size: 1.2em;
  color: #B6B6B6;
}

.jcf-form-wrap .input-checkbox .icon:before {
  content: '\e835';
}

.jcf-form-wrap .input-checkbox input:checked + .icon:before {
  content: '\e834';
}

.jcf-form-wrap .input-radio .icon:before {
  content: '\e836';
}

.jcf-form-wrap .input-radio input:checked + .icon:before {
  content: '\e837';
}

.jcf-form-wrap select {
  box-shadow: none;
  background-color: transparent;
  background-image: none;
  appearance: none;
  border: 1px solid #2B2B2B;
  border-radius: 0px;
  font-family: "Roboto", sans-serif;
  font-size: 15px;
