}

.jcf-form-wrap input[type="text"] + label:before,
.jcf-form-wrap input[type="email"] + label:before,
.jcf-form-wrap input[type="password"] + label:before {
  content: "";
  font-family: 'icomoon';
  font-size: 22.5px;
  position: absolute;
  top: 5px;
