textarea {
  color : black;
}


#right {
  flex: 0 0 325px;
}


.jcf-form-wrap {
  box-shadow: none;
}
.jcf-form-wrap input[type="text"]:valid + label:before, .jcf-form-wrap input[type="email"]:valid + label:before, .jcf-form-wrap input[type="password"]:valid + label:before {
  display: none;
}

textarea {
  width: 100%;
  background-color: transparent;
  color: $jcf-color-text-primary;
  font-family: $font-body;
  border: 1px solid $color-divider;
  padding: $spacing/2;
  resize: none;

  &:focus {
    outline: 3px solid $color-primary;
  }
}