.label {
  display: inline;
  padding: .2em .6em .3em;
  font-size: 75%;
  font-weight: 700;
  line-height: 1;
  color: #fff;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: .25em;
}

.label-primary {
  background-color: $jcf-color-primary;
  color : $jcf-color-text-primary !important;
}

.label-danger {
  background-color: $red;
  color : $jcf-color-text-primary !important;
}
